
'use client';

import { useState } from 'react';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Card, CardContent, CardDescription, CardHeader, CardTitle, CardFooter } from '@/components/ui/card';
import { Alert, AlertDescription, AlertTitle } from '@/components/ui/alert';
import { KeyRound, ShieldCheck, ShieldAlert } from 'lucide-react';

export default function AdminPage() {
  const [password, setPassword] = useState('');
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const handleSubmit = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    if (password === '123456') {
      setIsAuthenticated(true);
      setError(null);
    } else {
      setError('密码错误，请重试。');
      setPassword(''); // Clear password field on error
    }
  };

  if (isAuthenticated) {
    return (
      <div className="container mx-auto py-10">
        <Card className="max-w-2xl mx-auto shadow-lg">
          <CardHeader className="items-center text-center">
            <ShieldCheck className="w-12 h-12 text-green-500 mb-2 mx-auto" />
            <CardTitle className="text-2xl">管理后台</CardTitle>
            <CardDescription>欢迎来到管理区域。</CardDescription>
          </CardHeader>
          <CardContent>
            <p className="text-center text-lg">这里是管理后台的内容区域。</p>
            {/* 未来可以在此处添加更多管理功能 */}
          </CardContent>
        </Card>
      </div>
    );
  }

  return (
    <div className="flex items-center justify-center min-h-[calc(100vh-200px)]">
      <Card className="w-full max-w-md shadow-xl">
        <CardHeader className="text-center">
          <KeyRound className="mx-auto h-12 w-12 text-primary mb-3" />
          <CardTitle className="text-2xl font-bold">管理后台登录</CardTitle>
          <CardDescription>请输入密码以访问管理区域。</CardDescription>
        </CardHeader>
        <CardContent>
          <form onSubmit={handleSubmit} className="space-y-4">
            <Input
              type="password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              placeholder="请输入密码"
              className="text-center"
              aria-label="密码"
            />
            {error && (
              <Alert variant="destructive" className="text-sm">
                <ShieldAlert className="h-4 w-4" />
                <AlertTitle>登录失败</AlertTitle>
                <AlertDescription>{error}</AlertDescription>
              </Alert>
            )}
            <Button type="submit" className="w-full">
              登录
            </Button>
          </form>
        </CardContent>
         <CardFooter className="text-xs text-muted-foreground pt-6">
          <p className="text-center w-full">
            <strong>安全提示:</strong> 此处为演示目的使用客户端密码校验，
            <br />
            请勿在实际生产环境中使用此方式保护敏感数据。
          </p>
        </CardFooter>
      </Card>
    </div>
  );
}
